import React from 'react'
import App, { Container } from 'next/app'
import { Provider } from 'react-redux'
import { createStore } from 'redux'

import { validatePath } from '../routes'
import {
  SignIn,
  Navbar,
  Sidebar,
  Head,
} from '../feature'
import reducers from '../reducer'
import styles from '../theme/_app.scss';
import NotFound from './not-found';
import {
  isSignedIn,
  initialize,
  getUser,
} from '../firebase'

const store = createStore(reducers)

export default class MyApp extends App {
  static async getInitialProps({ Component, router, ctx }) {
    let pageProps = {}
    if (Component.getInitialProps) {
      pageProps = await Component.getInitialProps(ctx)
    }
    const isSignedInStatus = await isSignedIn()
    return { pageProps, router, isSignedIn: isSignedInStatus }
  }

  state = {
    isNotFoundRoute: false,
    isSignedIn: true,
  }

  static getDerivedStateFromProps(props, state) {
    console.log('props', props)
    let updateState = null
    updateState = {
      ...updateState,
      isSignedIn: props.isSignedIn
    }
    return updateState
  }

  componentDidMount() {
    initialize()
    this.checkUserStatus()
  }

  checkUserStatus = async () => {
    getUser((user) => {
      if (user.type === 'signed_in') {
        this.setState({
          isSignedIn: true,
        })
      } else {
        this.setState({
          isSignedIn: false,
        })
      }
      console.log('user', user)
    })
  }

  renderApp = () => {
    const {
      Component,
      pageProps,
      router,
      isSignedIn
    } = this.props
    const { isNotFoundRoute } = this.state
    const isCorrectRoute = router.asPath === '/' || validatePath(router.asPath)
    if (!isCorrectRoute) {
      return <NotFound />
    }
    if (isCorrectRoute && !isSignedIn) {
      return <SignIn isSignedIn={this._onSignedIn} />
    }
    return (
      <React.Fragment>
        <Head />
        {!isNotFoundRoute && (
          <React.Fragment>
            <Sidebar />
            <Navbar />
          </React.Fragment>
        )}
        <div className={styles.contentLayout}>
          <Component {...pageProps} />
        </div>
      </React.Fragment>
    )
  }

  render() {
    return (
      <Container>
        <Provider store={store}>
          {this.renderApp()}
        </Provider>
      </Container >
    )
  }
}